html, body {
  height: 100%;
  margin: 0;
  font-size: 16px;
  font-family: 'Times New Roman', Times, serif;
  font-weight: 400;
  line-height: 1.8em;
  color: #666;
} 


.pig1 , .pig2, .pig3{
  position: relative;
  opacity: 0.90;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  -webkit-animation-name: birdr;
          animation-name: birdr;
  -webkit-animation-duration: 3sec;
          animation-duration: 3sec;
}
.navbar{
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
  height: 10%;
  padding: 50px;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 24px;
  float: inherit;

}
.navbar .navbar-brand{
  font-display: inherit;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  color: aquamarine;
  font-size: 30px;
  text-transform: uppercase
}
.pig1{
  background-image: url('./img/pexels-photo-92866.jpeg');
  min-height: 1000px;
  opacity: 1;
  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
  
}
@-webkit-keyframes birdr{
  0%{background-image: url('./img/pexels-photo-92866.jpeg');}
  100%{background-image: url('./img/pexels-photo-374684.jpeg');}
}
@keyframes birdr{
  0%{background-image: url('./img/pexels-photo-92866.jpeg');}
  100%{background-image: url('./img/pexels-photo-374684.jpeg');}
}

.pig2{
  background-image: url('./img/pexels-photo-373934.jpeg');
  min-height: 400px;
  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
}

.pig3{
  background-image: url('./img/pexels-photo-773471.jpeg');
  min-height: 400px;
  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
}
.section{
  text-align: center;
  padding: 50px 80px;
  min-height: 600px;
}

.section-light{
  background-color: #f4f4f4;
  color: #666;
  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
}
.section-dark{
  background-color:#282e34 ;
  color: #ddd;
  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
}
.pigtext{

  position: absolute ;
  top: 50%;
  width: 100%;
  text-align: center;
  color: #000;
  font-size: 27px;
  letter-spacing: 8px;
  text-transform: uppercase;
}
.pigtext .border{
    background-color: #111;
    color: #fff;
    padding: 20px;
}
.pigtext .border.trans{
  background-color: transparent;
  
}



